<template>
    <div class="star" :class="starType">
        <span v-for="(itemClass, index) in itemClasses" :class="itemClass" class="star-item" :key="index"></span>
    </div>
</template>

<script>
const LENGTH = 5
const CLS_ON = 'on'
const CLS_HALF = 'half'
const CLS_OFF = 'off'
export default {
  props: {
    size: {
      type: Number
    },
    score: {
      type: Number
    }
  },
  computed: {
    starType () {
      return 'star-' + this.size
    },
    itemClasses () {
      const result = []
      // Math.floor(4.1 * 2) / 2 = 4
      // Math.floor(4.5 * 2) / 2 = 4.5
      const score = Math.floor(this.score * 2) / 2
      const hasDecimal = score % 1 !== 0
      const integer = Math.floor(score)
      for (let i = 0; i < integer; i++) {
        result.push(CLS_ON)
      }
      if (hasDecimal) {
        result.push(CLS_HALF)
      }
      while (result.length < LENGTH) {
        result.push(CLS_OFF)
      }
      return result
    }
  }
}
</script>

<style lang="stylus" scoped>
@import '../../common/stylus/mixin.styl';

.star
    // display: flex
    // align-items: center
    // justify-content: center
    font-size: 0
    .star-item
        display: inline-block
        background-repeat: no-repeat
    &.star-48
        .star-item
            width: 20px
            height: 20px
            margin-right: 22px
            background-size: 20px 20px

            &:last-child
                margin-right: 0
            &.on
                bg-img('star48_on')
            &.half
                bg-img('star48_half')
            &.off
                bg-img('star48_off')
    &.star-36
        .star-item
            width: 15px
            height: 15px
            margin-right: 6px
            background-size: 15px 15px

            &:last-child
                margin-right: 0
            &.on
                bg-img('star36_on')
            &.half
                bg-img('star36_half')
            &.off
                bg-img('star36_off')
    &.star-24
        .star-item
            width: 10px
            height: 10px
            margin-right: 3px
            background-size: 10px 10px

            &:last-child
                margin-right: 0
            &.on
                bg-img('star24_on')
            &.half
                bg-img('star24_half')
            &.off
                bg-img('star24_off')
</style>
